body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  height: 100%;
}


@font-face {font-family: "iconfont";
  src: url(../../static/media/iconfont.2994b44c.eot); /* IE9*/
  src: url(../../static/media/iconfont.2994b44c.eot#iefix) format('embedded-opentype'), 
  url('data:application/x-font-woff;charset=utf-8;base64,') format('woff'),
  url(../../static/media/iconfont.346f487a.ttf) format('truetype'), 
  url(../../static/media/iconfont.428c9e87.svg#iconfont) format('svg'); /* iOS 4.1- */
}

.iconfont {
  font-family:"iconfont" !important;
  font-size:16px;
  font-style:normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-share:before { content: "\E659"; }

.icon-read:before { content: "\E624"; }

.icon-pic:before { content: "\E688"; }

.icon-key:before { content: "\E648"; }

.icon-profile:before { content: "\E608"; }

.icon-shijian:before { content: "\E63D"; }

.icon-Fill:before { content: "\E503"; }

.icon-file:before { content: "\E953"; }

.icon-lock:before { content: "\E6A4"; }


.Detail {
  height: 100%;
  background-color: rgb(235, 235, 235);
}

.my-drawer {
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}

.my-drawer .am-drawer-sidebar {
  background-color: #fff;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}

.my-drawer .am-drawer-sidebar .am-list {
  width: 300px;
  padding: 0;
}

.my-accordion {
  overflow-y: scroll;
}
.am-list-item .am-list-line .am-list-extra {
  flex-basis: auto;
}

.login .logo {
  text-align: center;
  font-size: 28px;
  font-weight: 500;
  height: 130px;
  line-height: 130px;
}

.login .tip {
  font-size: 12px;
  text-align: center;
  margin: 8px auto;
}

.login .main-color {
  color: rgb(92, 156, 248);
}

.iconfont.icon {
  font-size: 24px;
}

.am-list-item .am-input-label.am-input-label-5 {
  width: 40px;
}
